<style lang="postcss" scoped>
.home-container{

}
</style>
<template>
<section class="home-container">
    <MasterPage title="统计报表">
        <div slot="title-icon">
            <Icon type="md-pie" />
        </div>
        <div slot="paddingContent">
            <Row>
                <Col span="8" offset="2">
                    <Card dis-hover class="card_charts">
                        销售完成度
                        <div id="myChart" :style="{width: '82%', height: '100%'}"></div>
                    </Card>
                </Col>
                <Col span="8" offset="3" class="card_charts">
                    <Card dis-hover>
                        主要销售组
                        <div id="myChart2" :style="{width: '90%', height: '100%'}"></div>
                    </Card>
                </Col>
                <Col span="22" offset="1" >
                    <Card dis-hover class="card_chart3">
                        <div id="myChart3" :style="{width: '80%', height: '700px'}"></div>
                    </Card>
                </Col>
            </Row>
        </div>
        
    </MasterPage>
</section>
</template>
<script>
import MasterPage from '@/components/Master'
export default {
    components:{
        MasterPage
    },
    data(){
        return {
            msg: ''
        }
    },
    mounted () {
        this.charts1();
        this.charts2();
        this.charts3();
    },
    methods:{
        charts1() {
            let myChart = this.$echarts.init(document.getElementById('myChart'))
            myChart.setOption({
                title: {
                text: '80%',
                x: 'center',
                y: 'center',
                textStyle: {
                    fontWeight: 'normal',
                    color: '#0580f2',
                    fontSize: '9'
                }
                },
                color: ['rgba(176, 212, 251, 1)'], 
                series: [{
                    name: 'Line 1',
                    type: 'pie',
                    clockWise: true,
                    radius: ['50%', '66%'],
                    itemStyle: {
                        normal: {
                            label: {
                                show: false
                            },
                            labelLine: {
                                show: false
                            }
                        }
                    },
                    hoverAnimation: false, 
                    data: [{
                        value: 8,
                        name: '01',
                        itemStyle: {
                            normal: {
                                color: { // 完成的圆环的颜色
                                    colorStops: [{
                                        offset: 0,
                                        color: '#00cefc' // 0% 处的颜色
                                    }, {
                                        offset: 1,
                                        color: '#367bec' // 100% 处的颜色
                                    }]
                                },
                                label: {
                                    show: false
                                },
                                labelLine: {
                                    show: false
                                }
                            } 
                            }
                        }, {
                            name: '02',
                            value: 20
                        }]
                    }]
            });
        },
        charts2 () {
            let myChart = this.$echarts.init(document.getElementById('myChart2'))
            var list = [
                [20, 80, 30, 65, 70, 75, 55, 65], 100, [{
                    "name": "东区A组",
                    "max": 100
                }, {
                    "name": "东区C组",
                    "max": 100
                }, {
                    "name": "南区B组",
                    "max": 100
                }, {
                    "name": "北区D组",
                    "max": 100
                }, {
                    "name": "西区A组",
                    "max": 100
                }, {
                    "name": "东区D组",
                    "max": 100
                }, {
                    "name": "南区E组",
                    "max": 100
                }, {
                    "name": "东区E组",
                    "max": 100
                }]
            ];
            var seriesData = list[0];
            var maxValue = list[1];
            var radarData = list[2];
            myChart.setOption({
                radar: [{
                    indicator: radarData,
                    center: ['50%', '50%'],
                    shape: 'polygon',
                    radius: '60%',
                    nameGap: 5,
                    axisLine: {
                        lineStyle: {
                            color: 'rgb(14,101,195)'
                        }
                    },
                    splitLine: {
                        show: false
                    },
                    splitArea: {
                        show: false
                    }
                }],
                series: [{
                    type: 'radar',
                    symbolSize: 0,
                    areaStyle: {
                        normal: {
                            color: new this.$echarts.graphic.LinearGradient(0, 0, 1, 0,
                                [{
                                    offset: 0,
                                    color: '#06d3d8'
                                }, {
                                    offset: 1,
                                    color: '#0018ff'
                                }], false)
                        }
                    },
                    lineStyle: {
                        normal: {
                            color: '#0018ff',
                            type: 'solid',
                            width: 1
                        }
                    },
                    data: [{
                        value: seriesData,
                        label: {
                            show: 'true'
                        }
                    }]
                }]
            })
        },
        charts3 () {
            let myChart = this.$echarts.init(document.getElementById('myChart3'));
            var dataAll = [389, 259, 262, 324, 232, 176, 196, 214, 133, 370];
	        var yAxisData = ["彭玉林", "李月", "李红梅", "胡中天", "董金", "吴卓能", "钟晨宇", "于峰", "张杰超", "王当龙"];
             myChart.setOption({
            backgroundColor: '#0f375f',
                title:[
                    {text:"各渠道销售占比",x: '2%', y: '1%',textStyle:{color:"#fff",fontSize:"14"}},
                    {text:"带看房销售人员排行",x: '40%', y: '1%',textStyle:{color:"#fff",fontSize:"14"}},
                    {text:"各楼盘销售占比",x: '2%', y: '50%',textStyle:{color:"#fff",fontSize:"14"}},
                ],
                grid: [
                    {x: '50%', y: '7%', width: '45%', height: '90%'},
                ],
                tooltip: {
                    formatter: '{b} ({c})'
                },
                xAxis: [
                    {gridIndex: 0, axisTick: {show:false},axisLabel: {show:false},splitLine: {show:false},axisLine: {show:false }},
                ],
                yAxis: [
                    {  gridIndex: 0, interval:0,data:yAxisData.reverse(),
                        axisTick: {show:false}, axisLabel: {show:true},splitLine: {show:false},
                        axisLine: {show:true,lineStyle:{color:"#6173a3"}},
                    }
                ],
                series: [
                    {
                        name: '各渠道销售占比',
                        type: 'pie',
                        radius : '30%',
                        center: ['22%', '25%'],
                        color:['#86c9f4','#4da8ec','#3a91d2','#005fa6','#315f97'],
                        data:[
                            {value:335, name:'客服电话'},
                            {value:310, name:'地面推广'},
                            {value:234, name:'媒体广告'},
                            {value:135, name:'线下活动'},
                            {value:95, name:'地铁广告'},
                        ],
                        labelLine:{normal:{show:false}},
                        itemStyle: {normal: {label:{ show: true,  formatter: '{b} \n ({d}%)', textStyle:{color:'#B1B9D3'}} },},
                    },
                    {
                        name: '各楼盘销售占比',
                        type: 'pie',
                        radius : '30%',
                        center: ['22%', '75%'],
                        color:['#86c9f4','#4da8ec','#3a91d2','#005fa6','#315f97'],
                        labelLine:{normal:{show:false}},
                        data:[
                            {value:335, name:'明洲东苑'},
                            {value:310, name:'明洲北苑'},
                            {value:234, name:'明洲西苑'},
                            {value:135, name:'明洲南苑'},
                        ],
                        itemStyle: {normal: {label:{ show: true,  formatter: '{b} \n ({d}%)', textStyle:{color:'#B1B9D3'}} },},
                    },
                    {
                        name: '带看房销售人员排行',
                        type: 'bar',xAxisIndex: 0,yAxisIndex: 0,barWidth:'45%',
                        itemStyle:{normal:{color:'#86c9f4'}},
                        label:{normal:{show:true, position:"right",textStyle:{color:"#9EA7C4"}}},
                        data: dataAll.sort(),
                    },
                
	             ]
             })
        }
    }
}
</script>
<style scoped>

.card_chart3{
    height: 700px;
    margin-top: 20px;
}
</style>



